import { useTranslation } from "react-i18next";
import { PartyPopper, X, Check, Bookmark, Loader2 } from "lucide-react";
import { Button } from "@/components/ui/button";
import { AIRecipeSuggestion } from "@/hooks/useAIRecipe";
import { useState } from "react";
import { Badge } from "@/components/ui/badge";

interface AIRecipeSharePromptProps {
  recipes: AIRecipeSuggestion[];
  /** Indices of dishes the user skipped during cooking — pre-deselected */
  skippedIndices?: number[];
  onShare: (selectedIndices: number[]) => void;
  onSkip: () => void;
  onSaveRecipe?: (recipe: AIRecipeSuggestion) => Promise<any>;
  /** Called when user un-saves a previously saved recipe */
  onUnsaveRecipe?: (id: string) => Promise<any>;
  isSaving?: boolean;
}

const AIRecipeSharePrompt = ({ recipes, skippedIndices = [], onShare, onSkip, onSaveRecipe, onUnsaveRecipe, isSaving }: AIRecipeSharePromptProps) => {
  const { t, i18n } = useTranslation();

  // Pre-deselect skipped dishes
  const [selectedIndices, setSelectedIndices] = useState<Set<number>>(
    new Set(recipes.map((_, i) => i).filter(i => !skippedIndices.includes(i)))
  );

  // Map: recipe index → saved recipe id (or true if saved without id)
  const [savedMap, setSavedMap] = useState<Map<number, string>>(new Map());
  const [savingIndex, setSavingIndex] = useState<number | null>(null);

  const handleToggleSave = async (index: number, e: React.MouseEvent) => {
    e.stopPropagation();
    if (!onSaveRecipe) return;

    if (savedMap.has(index)) {
      // Already saved → cancel save (delete)
      const savedId = savedMap.get(index);
      if (savedId && onUnsaveRecipe) {
        setSavingIndex(index);
        try {
          await onUnsaveRecipe(savedId);
          setSavedMap(prev => {
            const next = new Map(prev);
            next.delete(index);
            return next;
          });
        } finally {
          setSavingIndex(null);
        }
      }
    } else {
      // Not saved → save
      setSavingIndex(index);
      try {
        const result = await onSaveRecipe(recipes[index]);
        setSavedMap(prev => new Map(prev).set(index, result?.id || "saved"));
      } finally {
        setSavingIndex(null);
      }
    }
  };

  const getLocalName = (recipe: AIRecipeSuggestion) => {
    if (i18n.language === "en" && recipe.recipe_name_en) return recipe.recipe_name_en;
    if (i18n.language === "pt" && recipe.recipe_name_pt) return recipe.recipe_name_pt;
    return recipe.recipe_name;
  };

  const toggleDish = (index: number) => {
    const next = new Set(selectedIndices);
    if (next.has(index)) next.delete(index);
    else next.add(index);
    setSelectedIndices(next);
  };

  return (
    <div className="flex flex-col items-center gap-5 py-6">
      <div className="w-16 h-16 rounded-full bg-primary/10 flex items-center justify-center">
        <PartyPopper className="h-8 w-8 text-primary" />
      </div>

      <div className="text-center space-y-1.5">
        <h3 className="text-xl font-bold">
          {t("aiRecipe.cookingDone", "烹饪完成！🎉")}
        </h3>
        <p className="text-sm text-muted-foreground max-w-xs mx-auto">
          {t("aiRecipe.shareSelectDesc", "选择您想要分享到社区的菜品，每道菜需要拍摄成品照片")}
        </p>
      </div>

      {/* Dish selection list */}
      <div className="w-full max-w-sm space-y-2">
        {recipes.map((recipe, index) => {
          const isSkipped = skippedIndices.includes(index);
          const isSaved = savedMap.has(index);
          return (
            <button
              key={index}
              onClick={() => !isSkipped && toggleDish(index)}
              className={`w-full flex items-center gap-3 p-3 rounded-xl border transition-all text-left ${
                isSkipped
                  ? "border-border bg-muted/20 opacity-40 cursor-not-allowed"
                  : selectedIndices.has(index)
                  ? "border-primary bg-primary/5 ring-1 ring-primary/20"
                  : "border-border bg-muted/30 opacity-60"
              }`}
            >
              {/* Dish image */}
              <div className="w-14 h-14 rounded-lg overflow-hidden bg-muted shrink-0">
                {recipe.image_url ? (
                  <img src={recipe.image_url} alt={recipe.recipe_name} className="w-full h-full object-cover" />
                ) : (
                  <div className="w-full h-full flex items-center justify-center text-muted-foreground text-lg">
                    🍽️
                  </div>
                )}
              </div>

              {/* Dish info */}
              <div className="flex-1 min-w-0">
                <p className="font-medium text-sm truncate">{getLocalName(recipe)}</p>
                {recipe.subtitle && (
                  <p className="text-xs text-muted-foreground italic truncate mt-0.5">{recipe.subtitle}</p>
                )}
                <div className="flex items-center gap-1.5 mt-1">
                  <Badge variant="secondary" className="text-[10px] px-1.5 py-0">
                    {recipe.cook_time}{t("recipe.minuteShort", "分钟")}
                  </Badge>
                  {isSkipped && (
                    <Badge variant="outline" className="text-[10px] px-1.5 py-0 text-muted-foreground">
                      {t("aiRecipe.skipped", "已跳过")}
                    </Badge>
                  )}
                </div>
              </div>

              {/* Save / Unsave toggle button */}
              {onSaveRecipe && !isSkipped && (
                <button
                  onClick={(e) => handleToggleSave(index, e)}
                  disabled={savingIndex === index}
                  className={`p-1.5 rounded-lg shrink-0 transition-colors ${
                    isSaved
                      ? "text-primary"
                      : "text-muted-foreground hover:text-primary hover:bg-primary/10"
                  }`}
                >
                  {savingIndex === index ? (
                    <Loader2 className="h-4 w-4 animate-spin" />
                  ) : (
                    <Bookmark className={`h-4 w-4 ${isSaved ? "fill-primary" : ""}`} />
                  )}
                </button>
              )}

              {/* Selection indicator */}
              {!isSkipped && (
                <div className={`w-6 h-6 rounded-full border-2 flex items-center justify-center shrink-0 transition-colors ${
                  selectedIndices.has(index)
                    ? "bg-primary border-primary text-primary-foreground"
                    : "border-muted-foreground/30"
                }`}>
                  {selectedIndices.has(index) && <Check className="h-3.5 w-3.5" />}
                </div>
              )}
            </button>
          );
        })}
      </div>

      <div className="w-full max-w-sm space-y-3">
        <Button
          className="w-full gap-2 h-12"
          onClick={() => onShare(Array.from(selectedIndices).sort())}
          disabled={selectedIndices.size === 0}
        >
          {selectedIndices.size > 0
            ? t("aiRecipe.shareSelected", "分享 {{count}} 道菜到社区", { count: selectedIndices.size })
            : t("aiRecipe.shareToComm", "分享到社区")}
        </Button>
        <Button variant="outline" className="w-full gap-2" onClick={onSkip}>
          <X className="h-4 w-4" />
          {t("aiRecipe.noThanks", "暂时不分享")}
        </Button>
      </div>

      <p className="text-xs text-muted-foreground text-center max-w-xs">
        {t("aiRecipe.shareNote", "分享需要拍摄成品照片进行验证，以确保内容真实可靠")}
      </p>
    </div>
  );
};

export default AIRecipeSharePrompt;
