import { useState } from "react";
import { useTranslation } from "react-i18next";
import { ChefHat, Users, UtensilsCrossed, MessageSquare } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";

export interface RecipePreferences {
  difficulty?: "easy" | "medium" | "hard";
  servings?: number;
  dishCount?: number;
  notes?: string;
}

interface PreferencesStepProps {
  onConfirm: (prefs: RecipePreferences) => void;
  onSkip: () => void;
  initialValues?: RecipePreferences;
}

const SERVINGS_OPTIONS = [1, 2, 3, 4, 6];
const DISH_COUNT_OPTIONS = [1, 2, 3, 4, 5, 6];

const PreferencesStep = ({ onConfirm, onSkip, initialValues }: PreferencesStepProps) => {
  const { t } = useTranslation();
  const [difficulty, setDifficulty] = useState<"easy" | "medium" | "hard" | undefined>(initialValues?.difficulty);
  const [servings, setServings] = useState<number | undefined>(initialValues?.servings ?? 2);
  const [dishCount, setDishCount] = useState<number | undefined>(initialValues?.dishCount);
  const [notes, setNotes] = useState(initialValues?.notes || "");
  const isCustom = initialValues?.servings != null && !SERVINGS_OPTIONS.includes(initialValues.servings);
  const [showCustom, setShowCustom] = useState(isCustom);
  const [customServings, setCustomServings] = useState(isCustom ? String(initialValues?.servings) : "");

  const difficulties = [
    { value: "easy" as const, label: t("recipe.difficulty.easy", "简单"), desc: t("aiRecipe.diffEasyDesc", "适合零基础新手") },
    { value: "medium" as const, label: t("recipe.difficulty.medium", "中等"), desc: t("aiRecipe.diffMediumDesc", "有一点烹饪经验") },
    { value: "hard" as const, label: t("recipe.difficulty.hard", "困难"), desc: t("aiRecipe.diffHardDesc", "挑战高难度菜肴") },
  ];

  return (
    <div className="space-y-6">
      <div className="text-center space-y-1">
        <h3 className="text-lg font-semibold">
          {t("aiRecipe.preferences", "烹饪偏好")}
        </h3>
        <p className="text-sm text-muted-foreground">
          {t("aiRecipe.preferencesDesc", "设置您的偏好，AI 会据此推荐更合适的菜谱")}
        </p>
      </div>

      {/* Difficulty */}
      <div className="space-y-2">
        <div className="flex items-center gap-2 text-sm font-medium">
          <ChefHat className="h-4 w-4" />
          {t("aiRecipe.preferredDifficulty", "期望难度")}
        </div>
        <div className="grid grid-cols-3 gap-2">
          {difficulties.map((d) => (
            <button
              key={d.value}
              onClick={() => setDifficulty(difficulty === d.value ? undefined : d.value)}
              className={`p-3 rounded-lg border text-center transition-all ${
                difficulty === d.value
                  ? "border-primary bg-primary/10 ring-1 ring-primary"
                  : "hover:border-primary/50"
              }`}
            >
              <div className="text-sm font-medium">{d.label}</div>
              <div className="text-xs text-muted-foreground mt-0.5">{d.desc}</div>
            </button>
          ))}
        </div>
      </div>

      {/* Servings */}
      <div className="space-y-2">
        <div className="flex items-center gap-2 text-sm font-medium">
          <Users className="h-4 w-4" />
          {t("aiRecipe.preferredServings", "用餐人数")}
        </div>
        <div className="flex flex-wrap gap-2">
          {SERVINGS_OPTIONS.map((s) => (
            <Badge
              key={s}
              variant={servings === s && !showCustom ? "default" : "outline"}
              className="cursor-pointer px-4 py-2 text-sm"
              onClick={() => { setServings(s); setShowCustom(false); setCustomServings(""); }}
            >
              {s} {t("recipe.servingsUnit", "人份")}
            </Badge>
          ))}
          <Badge
            variant={showCustom ? "default" : "outline"}
            className="cursor-pointer px-4 py-2 text-sm"
            onClick={() => { setShowCustom(true); setServings(undefined); }}
          >
            {t("aiRecipe.customServings", "自定义")}
          </Badge>
        </div>
        {showCustom && (
          <div className="flex items-center gap-2 mt-2">
            <Input
              type="number"
              min={1}
              max={99}
              placeholder={t("aiRecipe.enterServings", "输入人数")}
              value={customServings}
              onChange={(e) => {
                setCustomServings(e.target.value);
                const n = parseInt(e.target.value);
                setServings(n > 0 ? n : undefined);
              }}
              className="w-28"
            />
            <span className="text-sm text-muted-foreground">{t("recipe.servingsUnit", "人份")}</span>
          </div>
        )}
      </div>

      {/* Dish Count */}
      <div className="space-y-2">
        <div className="flex items-center gap-2 text-sm font-medium">
          <UtensilsCrossed className="h-4 w-4" />
          {t("aiRecipe.dishCount", "菜品数量")}
        </div>
        <p className="text-xs text-muted-foreground">
          {t("aiRecipe.dishCountDesc", "不指定时 AI 会根据人数和食材自动判断")}
        </p>
        <div className="flex flex-wrap gap-2">
          <Badge
            variant={dishCount === undefined ? "default" : "outline"}
            className="cursor-pointer px-4 py-2 text-sm"
            onClick={() => setDishCount(undefined)}
          >
            {t("aiRecipe.autoDecide", "自动")}
          </Badge>
          {DISH_COUNT_OPTIONS.map((c) => (
            <Badge
              key={c}
              variant={dishCount === c ? "default" : "outline"}
              className="cursor-pointer px-4 py-2 text-sm"
              onClick={() => setDishCount(c)}
            >
              {c} {t("aiRecipe.dishUnit", "道菜")}
            </Badge>
          ))}
        </div>
      </div>

      {/* Notes */}
      <div className="space-y-2">
        <div className="flex items-center gap-2 text-sm font-medium">
          <MessageSquare className="h-4 w-4" />
          {t("aiRecipe.cookingNotes", "备注")}
        </div>
        <Textarea
          value={notes}
          onChange={(e) => setNotes(e.target.value)}
          placeholder={t("aiRecipe.cookingNotesPlaceholder", "如：不要太辣、想做一桌年夜饭、偏清淡口味等...")}
          rows={3}
          className="resize-none"
        />
      </div>

      <div className="flex gap-3 pt-2">
        <Button className="flex-1" onClick={() => onConfirm({ difficulty, servings, dishCount, notes: notes.trim() || undefined })}>
          {t("aiRecipe.confirmPreferences", "开始生成菜谱")}
        </Button>
      </div>
    </div>
  );
};

export default PreferencesStep;
