import { useState } from "react";
import { useTranslation } from "react-i18next";
import { Plus, Trash2, Edit2, Check, X } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Badge } from "@/components/ui/badge";
import { IdentifiedIngredient } from "@/hooks/useAIRecipe";

interface IngredientConfirmStepProps {
  ingredients: IdentifiedIngredient[];
  onChange: (ingredients: IdentifiedIngredient[]) => void;
}

const IngredientConfirmStep = ({ ingredients, onChange }: IngredientConfirmStepProps) => {
  const { t } = useTranslation();
  const [editIndex, setEditIndex] = useState<number | null>(null);
  const [editName, setEditName] = useState("");
  const [editAmount, setEditAmount] = useState("");
  const [editUnit, setEditUnit] = useState("");
  const [newName, setNewName] = useState("");

  const handleEdit = (index: number) => {
    setEditIndex(index);
    setEditName(ingredients[index].name);
    setEditAmount(ingredients[index].estimated_amount || "");
    setEditUnit(ingredients[index].unit || "");
  };

  const handleSaveEdit = () => {
    if (editIndex === null || !editName.trim()) return;
    const updated = [...ingredients];
    updated[editIndex] = { ...updated[editIndex], name: editName.trim(), estimated_amount: editAmount.trim(), unit: editUnit.trim() };
    onChange(updated);
    setEditIndex(null);
  };

  const handleDelete = (index: number) => {
    onChange(ingredients.filter((_, i) => i !== index));
  };

  const handleAdd = () => {
    if (!newName.trim()) return;
    // Split by Chinese or English comma, or spaces, filter empty
    const parts = newName.split(/[，,\s]+/).map(s => s.trim()).filter(Boolean);
    if (parts.length === 0) return;
    onChange([...ingredients, ...parts.map(name => ({ name, estimated_amount: "", unit: "" }))]);
    setNewName("");
  };

  return (
    <div className="space-y-4">
      <div className="text-center space-y-1 mb-4">
        <h3 className="text-lg font-semibold">
          {t("aiRecipe.confirmIngredients", "确认食材")}
        </h3>
        <p className="text-sm text-muted-foreground">
          {t("aiRecipe.confirmIngredientsDesc", "确认 AI 识别的食材，您可以添加、编辑或删除")}
        </p>
      </div>

      <div className="space-y-2">
        {ingredients.map((item, index) => (
          <div
            key={index}
            className="flex items-center gap-2 p-3 rounded-lg border bg-card"
          >
            {editIndex === index ? (
              <>
                <Input
                  value={editName}
                  onChange={(e) => setEditName(e.target.value)}
                  placeholder={t("aiRecipe.ingredientName", "食材名称")}
                  className="flex-1 h-8"
                  autoFocus
                />
                <Input
                  value={editAmount}
                  onChange={(e) => setEditAmount(e.target.value)}
                  placeholder={t("aiRecipe.amount", "数量")}
                  className="w-16 h-8"
                />
                <Input
                  value={editUnit}
                  onChange={(e) => setEditUnit(e.target.value)}
                  placeholder={t("aiRecipe.unit", "单位")}
                  className="w-14 h-8"
                />
                <Button size="icon" variant="ghost" className="h-8 w-8" onClick={handleSaveEdit}>
                  <Check className="h-4 w-4" />
                </Button>
                <Button size="icon" variant="ghost" className="h-8 w-8" onClick={() => setEditIndex(null)}>
                  <X className="h-4 w-4" />
                </Button>
              </>
            ) : (
              <>
                <span className="flex-1 text-sm font-medium">{item.name}</span>
                {item.estimated_amount && (
                  <Badge variant="outline" className="text-xs">
                    {item.estimated_amount}{item.unit || ""}
                  </Badge>
                )}
                <Button size="icon" variant="ghost" className="h-8 w-8" onClick={() => handleEdit(index)}>
                  <Edit2 className="h-3.5 w-3.5" />
                </Button>
                <Button size="icon" variant="ghost" className="h-8 w-8 text-destructive" onClick={() => handleDelete(index)}>
                  <Trash2 className="h-3.5 w-3.5" />
                </Button>
              </>
            )}
          </div>
        ))}
      </div>

      {/* Add new ingredient */}
      <div className="flex gap-2">
        <Input
          value={newName}
          onChange={(e) => setNewName(e.target.value)}
          placeholder={t("aiRecipe.addIngredientPlaceholder", "添加食材...")}
          className="flex-1"
          onKeyDown={(e) => e.key === "Enter" && handleAdd()}
        />
        <Button onClick={handleAdd} disabled={!newName.trim()} size="icon">
          <Plus className="h-4 w-4" />
        </Button>
      </div>
    </div>
  );
};

export default IngredientConfirmStep;
