import { useState, useRef } from "react";
import { useTranslation } from "react-i18next";
import { Camera, Loader2, ImageIcon, PenLine, X, Plus } from "lucide-react";
import { Button } from "@/components/ui/button";
import CameraCapture from "./CameraCapture";

const MAX_PHOTOS = 5;

interface IngredientPhotoStepProps {
  onPhotosUploaded: (imageUrls: string[]) => void;
  onSkipToManual: () => void;
  uploadImage: (file: File, folder: string) => Promise<string | null>;
  isUploading?: boolean;
  initialImageUrls?: string[];
  onRequireLogin?: () => boolean;
}

interface PhotoItem {
  id: string;
  preview: string;
  url: string | null;
  uploading: boolean;
}

const IngredientPhotoStep = ({ onPhotosUploaded, onSkipToManual, uploadImage, isUploading, initialImageUrls, onRequireLogin }: IngredientPhotoStepProps) => {
  const { t } = useTranslation();
  const [photos, setPhotos] = useState<PhotoItem[]>(() => {
    if (initialImageUrls && initialImageUrls.length > 0) {
      return initialImageUrls.map((url, i) => ({
        id: `restored-${i}`,
        preview: url,
        url,
        uploading: false,
      }));
    }
    return [];
  });
  const [showCamera, setShowCamera] = useState(false);
  const fileRef = useRef<HTMLInputElement>(null);

  const uploadingCount = photos.filter(p => p.uploading).length;
  const allUploaded = photos.length > 0 && photos.every(p => p.url !== null);

  const handleFiles = async (files: FileList | File[]) => {
    if (onRequireLogin && !onRequireLogin()) return;

    const fileArray = Array.from(files);
    const remaining = MAX_PHOTOS - photos.length;
    const toAdd = fileArray.slice(0, remaining).filter(f => f.size <= 10 * 1024 * 1024);

    if (toAdd.length === 0) return;

    const newItems: PhotoItem[] = toAdd.map(f => ({
      id: `${Date.now()}-${Math.random().toString(36).slice(2)}`,
      preview: URL.createObjectURL(f),
      url: null,
      uploading: true,
    }));

    setPhotos(prev => [...prev, ...newItems]);

    await Promise.all(
      toAdd.map(async (file, i) => {
        const url = await uploadImage(file, "ai-recipe-ingredients");
        if (url) {
          setPhotos(prev =>
            prev.map(p => p.id === newItems[i].id ? { ...p, url, uploading: false } : p)
          );
        } else {
          setPhotos(prev => prev.filter(p => p.id !== newItems[i].id));
        }
      })
    );
  };

  const handleCameraCapture = (file: File) => {
    setShowCamera(false);
    handleFiles([file]);
  };

  const handleOpenCamera = () => {
    if (onRequireLogin && !onRequireLogin()) return;
    setShowCamera(true);
  };

  const removePhoto = (id: string) => {
    setPhotos(prev => prev.filter(p => p.id !== id));
  };

  const handleSubmit = () => {
    if (onRequireLogin && !onRequireLogin()) return;
    const urls = photos.map(p => p.url).filter(Boolean) as string[];
    if (urls.length > 0) {
      onPhotosUploaded(urls);
    }
  };

  return (
    <>
      {showCamera && (
        <CameraCapture
          onCapture={handleCameraCapture}
          onClose={() => setShowCamera(false)}
        />
      )}

      <div className="flex flex-col items-center justify-center gap-6 py-8">
        <div className="text-center space-y-2">
          <div className="w-20 h-20 rounded-full bg-primary/10 flex items-center justify-center mx-auto">
            <Camera className="h-10 w-10 text-primary" />
          </div>
          <h3 className="text-lg font-semibold">
            {t("aiRecipe.photoTitle", "拍摄您的食材")}
          </h3>
          <p className="text-sm text-muted-foreground max-w-xs">
            {t("aiRecipe.photoDescMulti", "拍照或上传食材照片（最多5张），AI 将自动识别食材")}
          </p>
        </div>

        {/* Photo grid */}
        {photos.length > 0 && (
          <div className="w-full max-w-sm grid grid-cols-3 gap-2">
            {photos.map((photo) => (
              <div key={photo.id} className="relative aspect-square rounded-lg overflow-hidden border bg-muted">
                <img src={photo.preview} alt="Preview" className="w-full h-full object-cover" />
                {photo.uploading && (
                  <div className="absolute inset-0 bg-background/60 flex items-center justify-center">
                    <Loader2 className="h-5 w-5 animate-spin text-primary" />
                  </div>
                )}
                <button
                  onClick={() => removePhoto(photo.id)}
                  className="absolute top-1 right-1 w-5 h-5 rounded-full bg-black/60 text-white flex items-center justify-center"
                >
                  <X className="h-3 w-3" />
                </button>
              </div>
            ))}
            {photos.length < MAX_PHOTOS && (
              <button
                onClick={() => fileRef.current?.click()}
                className="aspect-square rounded-lg border-2 border-dashed border-muted-foreground/30 flex flex-col items-center justify-center gap-1 text-muted-foreground hover:border-primary/50 hover:text-primary transition-colors"
                disabled={uploadingCount > 0}
              >
                <Plus className="h-5 w-5" />
                <span className="text-xs">{photos.length}/{MAX_PHOTOS}</span>
              </button>
            )}
          </div>
        )}

        {/* Action buttons */}
        {photos.length === 0 ? (
          <div className="flex gap-3 w-full max-w-sm">
            <Button
              variant="default"
              className="flex-1 gap-2"
              onClick={handleOpenCamera}
            >
              <Camera className="h-4 w-4" />
              {t("aiRecipe.takePhoto", "拍照")}
            </Button>
            <Button
              variant="outline"
              className="flex-1 gap-2"
              onClick={() => fileRef.current?.click()}
            >
              <ImageIcon className="h-4 w-4" />
              {t("aiRecipe.uploadPhoto", "相册")}
            </Button>
          </div>
        ) : (
          <div className="flex gap-3 w-full max-w-sm">
            <Button
              variant="outline"
              className="flex-1 gap-2"
              onClick={handleOpenCamera}
              disabled={photos.length >= MAX_PHOTOS || uploadingCount > 0}
            >
              <Camera className="h-4 w-4" />
              {t("aiRecipe.addMore", "继续拍照")}
            </Button>
            <Button
              className="flex-1 gap-2"
              onClick={handleSubmit}
              disabled={!allUploaded || uploadingCount > 0}
            >
              {uploadingCount > 0 ? (
                <>
                  <Loader2 className="h-4 w-4 animate-spin" />
                  {t("common.uploading", "上传中...")}
                </>
              ) : (
                t("aiRecipe.startIdentify", "开始识别")
              )}
            </Button>
          </div>
        )}

        {/* Skip to manual input */}
        <Button
          variant="ghost"
          className="gap-2 text-muted-foreground"
          onClick={onSkipToManual}
          disabled={uploadingCount > 0}
        >
          <PenLine className="h-4 w-4" />
          {t("aiRecipe.skipToManual", "跳过，手动输入食材")}
        </Button>

        <input
          ref={fileRef}
          type="file"
          accept="image/*"
          multiple
          className="hidden"
          onChange={(e) => {
            const files = e.target.files;
            if (files?.length) handleFiles(files);
            e.target.value = "";
          }}
        />
      </div>
    </>
  );
};

export default IngredientPhotoStep;
