import { useState } from "react";
import { useTranslation } from "react-i18next";
import { Clock, Users, ChefHat, PlayCircle, List, Sparkles, Globe, SkipForward, Video, Loader2 } from "lucide-react";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { Separator } from "@/components/ui/separator";
import { AIRecipeSuggestion } from "@/hooks/useAIRecipe";
import RecipeCookingMode from "@/components/community/recipe/RecipeCookingMode";
import ImagePreviewModal from "@/components/ImagePreviewModal";
import { useRecipeVideo } from "@/hooks/useRecipeVideo";
import { useWatermarkedVideo } from "@/hooks/useWatermarkedVideo";
import xiaoxiongLogo from "@/assets/xiaoxiong-logo-clean.png";
interface AIRecipeCookingViewProps {
  recipe: AIRecipeSuggestion;
  ingredientImageUrl?: string;
  onComplete: () => void;
  onBack: () => void;
  onSkip?: () => void;
}

type ViewMode = "guided" | "full";

const AIRecipeCookingView = ({ recipe, ingredientImageUrl, onComplete, onBack, onSkip }: AIRecipeCookingViewProps) => {
  const { t, i18n } = useTranslation();
  const [viewMode, setViewMode] = useState<ViewMode>("guided");
  const [guidedStarted, setGuidedStarted] = useState(false);
  const [previewImageUrl, setPreviewImageUrl] = useState<string | null>(null);
  const { videoTasks, generateVideo, clearTask } = useRecipeVideo();
  // Track whether video was generated in THIS session (don't show persisted old videos)
  const [videoGeneratedThisSession, setVideoGeneratedThisSession] = useState(false);

  const videoKey = recipe.recipe_name;
  const videoTask = videoGeneratedThisSession ? videoTasks[videoKey] : undefined;

  // Watermark the video
  const rawVideoUrl = videoTask?.status === "succeeded" ? videoTask.videoUrl : null;
  const { watermarkedUrl, isProcessing: isWatermarking } = useWatermarkedVideo(rawVideoUrl, xiaoxiongLogo);

  const handleGenerateVideo = () => {
    const imgUrl = recipe.image_url;
    if (!imgUrl) return;
    setVideoGeneratedThisSession(true);
    const recipeName = recipe.recipe_name_en || recipe.recipe_name;
    const stepsText = recipe.steps
      .slice(0, 6)
      .map(s => s.description_en || s.description)
      .filter(Boolean)
      .join(". ");
    const prompt = `Step-by-step cooking tutorial video showing the complete preparation process of ${recipeName}. Show hands chopping ingredients, mixing in a bowl, cooking in a pan with oil sizzling. ${stepsText || 'preparing ingredients, cooking, and plating'}. Kitchen counter with fresh ingredients, cutting board, knife, pots and pans visible. Warm natural kitchen lighting, steam rising from the pan, close-up of each cooking step, smooth transitions between steps. --duration 5 --camerafixed true`;
    generateVideo(videoKey, imgUrl, prompt);
  };

  const getName = () => {
    if (i18n.language === "en" && recipe.recipe_name_en) return recipe.recipe_name_en;
    if (i18n.language === "pt" && recipe.recipe_name_pt) return recipe.recipe_name_pt;
    return recipe.recipe_name;
  };

  const getDifficultyLabel = (d: string) => {
    switch (d) {
      case "easy": return t("recipe.difficulty.easy", "简单");
      case "medium": return t("recipe.difficulty.medium", "中等");
      case "hard": return t("recipe.difficulty.hard", "困难");
      default: return d;
    }
  };

  const getDifficultyColor = (d: string) => {
    switch (d) {
      case "easy": return "bg-green-500/20 text-green-700 dark:text-green-400 border-green-500/30";
      case "medium": return "bg-yellow-500/20 text-yellow-700 dark:text-yellow-400 border-yellow-500/30";
      case "hard": return "bg-red-500/20 text-red-700 dark:text-red-400 border-red-500/30";
      default: return "bg-muted text-muted-foreground";
    }
  };

  const steps = recipe.steps.map((s, i) => ({ ...s, order: s.order || i + 1 }));

  // Guided mode - cooking phase (step-by-step)
  if (viewMode === "guided" && guidedStarted) {
    return (
      <Card className="overflow-hidden">
        <RecipeCookingMode
          steps={steps}
          onExit={() => setGuidedStarted(false)}
          onComplete={onComplete}
        />
      </Card>
    );
  }

  // Mode toggle
  const ModeToggle = () => (
    <div className="flex justify-end">
      <div className="inline-flex h-9 items-center rounded-md bg-muted p-1 text-muted-foreground">
        <button
          onClick={() => { setViewMode("guided"); setGuidedStarted(false); }}
          className={`inline-flex items-center justify-center whitespace-nowrap rounded-sm px-3 py-1.5 text-sm font-medium transition-all gap-1.5 ${viewMode === "guided" ? "bg-background text-foreground shadow-sm" : ""}`}
        >
          <PlayCircle className="h-4 w-4" />
          {t("recipe.guidedMode", "流程模式")}
        </button>
        <button
          onClick={() => setViewMode("full")}
          className={`inline-flex items-center justify-center whitespace-nowrap rounded-sm px-3 py-1.5 text-sm font-medium transition-all gap-1.5 ${viewMode === "full" ? "bg-background text-foreground shadow-sm" : ""}`}
        >
          <List className="h-4 w-4" />
          {t("recipe.fullMode", "完整模式")}
        </button>
      </div>
    </div>
  );

  // Overview content (shared between guided overview and full mode top)
  const RecipeHeader = () => (
    <Card className="overflow-hidden">

      {/* Recipe image - always show the AI generated image */}
      {recipe.image_url ? (
        <div className="relative w-full aspect-[16/9] bg-muted">
          <img
            src={recipe.image_url}
            alt={getName()}
            className="w-full h-full object-cover"
          />
          <div className="absolute bottom-2 right-2 flex items-center gap-1.5">
            {/* Generate video button */}
            {!videoTask?.videoUrl && (
              <button
                onClick={handleGenerateVideo}
                disabled={videoTask?.status === "creating" || videoTask?.status === "processing"}
                className="inline-flex items-center gap-1 text-[10px] bg-black/60 hover:bg-black/80 text-white px-2 py-1 rounded-full transition-colors disabled:opacity-50"
              >
                {videoTask?.status === "creating" || videoTask?.status === "processing" ? (
                  <>
                    <Loader2 className="h-2.5 w-2.5 animate-spin" />
                    {t("aiRecipe.generatingVideo", "生成视频中...")}
                  </>
                ) : (
                  <>
                    <Video className="h-2.5 w-2.5" />
                    {t("aiRecipe.generateVideo", "生成视频")}
                  </>
                )}
              </button>
            )}
            <span className="inline-flex items-center gap-1 text-[10px] bg-black/50 text-white px-2 py-0.5 rounded-full">
              {recipe.image_source === "ai" ? (
                <>
                  <Sparkles className="h-2.5 w-2.5" />
                  AI 生成
                </>
              ) : (
                <>
                  <Globe className="h-2.5 w-2.5" />
                  来自网络
                </>
              )}
            </span>
          </div>
          {videoTask?.status === "failed" && (
            <div className="absolute top-2 left-2 right-2">
              <div className="bg-destructive/90 text-destructive-foreground text-xs px-2 py-1 rounded">
                {videoTask.error || t("aiRecipe.videoFailed", "视频生成失败")}
              </div>
            </div>
          )}
        </div>
      ) : null}

      {/* Video player - shown below the image when video is ready */}
      {videoTask?.status === "succeeded" && videoTask.videoUrl && (
        <div className="px-4 pt-3">
          {isWatermarking ? (
            <div className="w-full aspect-video rounded-xl bg-muted flex items-center justify-center">
              <Loader2 className="h-5 w-5 animate-spin text-muted-foreground" />
            </div>
          ) : (
            <video
              src={watermarkedUrl || videoTask.videoUrl}
              controls
              autoPlay
              muted
              loop
              playsInline
              className="w-full rounded-xl"
            />
          )}
        </div>
      )}

      <CardHeader className="pb-3">
        <h2 className="text-xl font-bold">{getName()}</h2>
        <div className="flex flex-wrap items-center gap-3 mt-3">
          {recipe.cook_time && (
            <div className="flex items-center gap-1.5 text-sm text-muted-foreground">
              <Clock className="h-4 w-4" />
              <span>{recipe.cook_time} {t("recipe.minutes", "分钟")}</span>
            </div>
          )}
          {recipe.servings && (
            <div className="flex items-center gap-1.5 text-sm text-muted-foreground">
              <Users className="h-4 w-4" />
              <span>{recipe.servings} {t("recipe.servingsUnit", "人份")}</span>
            </div>
          )}
          {recipe.difficulty && (
            <Badge variant="outline" className={getDifficultyColor(recipe.difficulty)}>
              <ChefHat className="h-3 w-3 mr-1" />
              {getDifficultyLabel(recipe.difficulty)}
            </Badge>
          )}
        </div>
      </CardHeader>

      <CardContent className="space-y-6">
        {/* Introduction */}
        {(() => {
          const intro = i18n.language === "en" && recipe.introduction_en ? recipe.introduction_en
            : i18n.language === "pt" && recipe.introduction_pt ? recipe.introduction_pt
            : recipe.introduction;
          return intro && <p className="text-sm text-muted-foreground whitespace-pre-line">{intro}</p>;
        })()}

        <Separator />

        {/* Ingredients */}
        {recipe.ingredients.length > 0 && (
          <div>
            <h3 className="font-semibold text-sm mb-3">
              {t("recipe.ingredients", "食材")} ({recipe.ingredients.length})
            </h3>
            <div className="flex items-center text-xs text-muted-foreground font-medium px-2 py-1.5 bg-muted/50 rounded-t-md border border-border/50">
              <span className="flex-1">{t("recipe.ingredientName", "名称")}</span>
              <span className="w-24 text-right">{t("recipe.amount", "用量")}</span>
            </div>
            <ul className="border border-t-0 border-border/50 rounded-b-md divide-y divide-border/50">
              {recipe.ingredients.map((ingredient, index) => {
                const localName = i18n.language === "en" && ingredient.name_en ? ingredient.name_en
                  : i18n.language === "pt" && ingredient.name_pt ? ingredient.name_pt
                  : ingredient.name;
                return (
                  <li key={index} className="flex items-center text-sm px-2 py-2">
                    <span className="flex-1 min-w-0 truncate">{localName}</span>
                    <span className="w-24 text-right text-muted-foreground shrink-0">
                      {ingredient.amount} {i18n.language === "en" && ingredient.unit_en ? ingredient.unit_en
                        : i18n.language === "pt" && ingredient.unit_pt ? ingredient.unit_pt
                        : ingredient.unit}
                    </span>
                  </li>
                );
              })}
            </ul>
          </div>
        )}

        {/* Guided mode: Start cooking button + skip */}
        {viewMode === "guided" && (
          <>
            <Separator />
            <Button
              size="lg"
              className="w-full gap-2 h-14 text-lg"
              onClick={() => setGuidedStarted(true)}
            >
              <PlayCircle className="h-6 w-6" />
              {t("recipe.startCooking", "开始烹饪")}
              <Badge variant="secondary" className="ml-2">
                {steps.length} {t("recipe.stepsCount", "步")}
              </Badge>
            </Button>
            {onSkip && (
              <Button
                variant="ghost"
                className="w-full gap-2 text-muted-foreground"
                onClick={onSkip}
              >
                <SkipForward className="h-4 w-4" />
                {t("aiRecipe.skipDish", "跳过这道菜")}
              </Button>
            )}
          </>
        )}

        {/* Full mode: Steps inline */}
        {viewMode === "full" && (
          <>
            <Separator />
            <div>
              <h3 className="font-semibold text-sm mb-3">
                {t("recipe.step.steps", "步骤")} ({steps.length})
              </h3>
              <ol className="space-y-4">
                {steps.map((step, index) => {
                  const localDesc = i18n.language === "en" && step.description_en ? step.description_en
                    : i18n.language === "pt" && step.description_pt ? step.description_pt
                    : step.description;
                  return (
                    <li key={index} className="flex gap-3">
                      <div className="flex-shrink-0 w-6 h-6 rounded-full bg-primary text-primary-foreground flex items-center justify-center text-xs font-bold">
                        {step.order}
                      </div>
                      <div className="flex-1 space-y-2">
                        <p className="text-sm">{localDesc}</p>
                        {(() => {
                          const imgs = (step.image_urls && step.image_urls.length > 0) ? step.image_urls : step.image_url ? [step.image_url] : [];
                          return imgs.length > 0 && (
                            <div className={`flex gap-2 ${imgs.length === 1 ? "" : "flex-wrap"}`}>
                              {imgs.map((url, imgIdx) => (
                                <img
                                  key={imgIdx}
                                  src={url}
                                  alt={`Step ${step.order} - ${imgIdx + 1}`}
                                  className={`rounded-lg max-h-48 object-cover cursor-pointer ${imgs.length === 1 ? "w-full" : "w-[calc(50%-0.25rem)]"}`}
                                  onClick={() => setPreviewImageUrl(url)}
                                />
                              ))}
                            </div>
                          );
                        })()}
                      </div>
                    </li>
                  );
                })}
              </ol>
            </div>
          </>
        )}

        {viewMode === "full" && (
          <Button
            size="lg"
            variant="outline"
            className="w-full gap-2"
            onClick={onComplete}
          >
            {t("recipe.completeCooking", "完成烹饪")}
          </Button>
        )}
      </CardContent>
    </Card>
  );

  return (
    <div className="space-y-4">
      <ModeToggle />
      <RecipeHeader />
      <ImagePreviewModal
        imageUrl={previewImageUrl || ""}
        isOpen={!!previewImageUrl}
        onClose={() => setPreviewImageUrl(null)}
      />
    </div>
  );
};

export default AIRecipeCookingView;
