import { useState } from "react";
import { useTranslation } from "react-i18next";
import { Clock, Users, ChefHat, AlertTriangle, RefreshCw, Sparkles, Globe, Check, UtensilsCrossed, Info, Pencil, History, ChevronDown } from "lucide-react";
import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { Skeleton } from "@/components/ui/skeleton";
import { AIRecipeSuggestion } from "@/hooks/useAIRecipe";
import { RecipePreferences } from "@/components/ai-recipe/PreferencesStep";

interface RecipeSelectStepProps {
  recipes: AIRecipeSuggestion[];
  recipeHistory?: AIRecipeSuggestion[][];
  onSelectHistoryMeal?: (meal: AIRecipeSuggestion[]) => void;
  onConfirmMeal: () => void;
  onRegenerate?: (updatedNotes?: string) => void;
  isRegenerating?: boolean;
  preferences?: RecipePreferences;
  isLoadingImages?: boolean;
}

const RecipeSelectStep = ({ recipes, recipeHistory = [], onSelectHistoryMeal, onConfirmMeal, onRegenerate, isRegenerating, preferences, isLoadingImages }: RecipeSelectStepProps) => {
  const { t, i18n } = useTranslation();
  const [editingNotes, setEditingNotes] = useState(false);
  const [notesValue, setNotesValue] = useState(preferences?.notes || "");
  const [showHistory, setShowHistory] = useState(false);

  const getLocalizedName = (recipe: AIRecipeSuggestion) => {
    if (i18n.language === "en" && recipe.recipe_name_en) return recipe.recipe_name_en;
    if (i18n.language === "pt" && recipe.recipe_name_pt) return recipe.recipe_name_pt;
    return recipe.recipe_name;
  };

  const getLocalizedSubtitle = (recipe: AIRecipeSuggestion) => {
    if (i18n.language === "en" && recipe.subtitle_en) return recipe.subtitle_en;
    if (i18n.language === "pt" && recipe.subtitle_pt) return recipe.subtitle_pt;
    return recipe.subtitle;
  };

  const difficultyLabel = (d: string) => {
    switch (d) {
      case "easy": return t("recipe.difficulty.easy", "简单");
      case "medium": return t("recipe.difficulty.medium", "中等");
      case "hard": return t("recipe.difficulty.hard", "困难");
      default: return d;
    }
  };

  const totalCookTime = recipes.reduce((sum, r) => sum + (r.cook_time || 0), 0);

  return (
    <div className="space-y-4">
      {/* Meal Plan Header */}
      <div className="text-center space-y-2 mb-4">
        <div className="inline-flex items-center gap-2 px-3 py-1.5 bg-primary/10 rounded-full">
          <UtensilsCrossed className="h-4 w-4 text-primary" />
          <span className="text-sm font-medium text-primary">
            {t("aiRecipe.mealPlan", "完整菜单")}
          </span>
        </div>
        <h3 className="text-lg font-semibold">
          {t("aiRecipe.mealPlanTitle", "AI 为您设计了一桌菜")}
        </h3>
        <p className="text-sm text-muted-foreground">
          {t("aiRecipe.mealPlanDesc", "共 {{count}} 道菜，总计约 {{time}} 分钟", { count: recipes.length, time: totalCookTime })}
        </p>
        {/* Dish count explanation */}
        {preferences && (
          <div className="inline-flex items-start gap-1.5 text-xs text-muted-foreground bg-muted/50 rounded-lg px-3 py-2 mt-1 text-left max-w-sm mx-auto">
            <Info className="h-3.5 w-3.5 mt-0.5 shrink-0" />
            <span>
              {preferences.dishCount
                ? t("aiRecipe.dishCountExplain", "按照您指定的 {{count}} 道菜生成", { count: preferences.dishCount })
                : preferences.notes
                  ? t("aiRecipe.dishCountFromNotes", "根据您的备注「{{notes}}」，AI 为您规划了 {{count}} 道菜", { notes: preferences.notes.length > 20 ? preferences.notes.slice(0, 20) + "…" : preferences.notes, count: recipes.length })
                  : t("aiRecipe.dishCountAuto", "AI 根据 {{servings}} 人份的食材自动规划了 {{count}} 道菜", { servings: preferences.servings || recipes[0]?.servings || "?", count: recipes.length })
              }
            </span>
          </div>
        )}
      </div>

      {/* All dishes in the meal */}
      <div className="space-y-3">
        {recipes.map((recipe, index) => (
          <Card key={index} className="overflow-hidden">
            {/* Image area: loading skeleton → actual image */}
            {recipe.image_url ? (
              <div className="relative w-full aspect-[16/9] bg-muted">
                <img
                  src={recipe.image_url}
                  alt={getLocalizedName(recipe)}
                  className="w-full h-full object-cover"
                  loading="lazy"
                />
                <Badge variant="secondary" className="absolute top-2 left-2 text-xs font-bold">
                  {t("aiRecipe.dishNumber", "第 {{n}} 道", { n: index + 1 })}
                </Badge>
                <Badge variant="secondary" className="absolute bottom-2 right-2 text-[10px] gap-1 opacity-80">
                  {recipe.image_source === "ai" ? (
                    <><Sparkles className="h-2.5 w-2.5" />{t("aiRecipe.imageSourceAI", "AI 生成")}</>
                  ) : (
                    <><Globe className="h-2.5 w-2.5" />{t("aiRecipe.imageSourceWeb", "来自网络")}</>
                  )}
                </Badge>
              </div>
            ) : (
              <div className="relative w-full aspect-[16/9] overflow-hidden bg-muted">
                {isLoadingImages ? (
                  <>
                    <Skeleton className="absolute inset-0 w-full h-full rounded-none" />
                    <div className="absolute inset-0 flex flex-col items-center justify-center gap-1.5 z-10">
                      <Sparkles className="h-5 w-5 text-primary animate-pulse" />
                      <span className="text-xs text-muted-foreground">
                        {t("aiRecipe.generatingImage", "AI 正在生成图片...")}
                      </span>
                    </div>
                  </>
                ) : (
                  <div className="absolute inset-0 flex items-center justify-center">
                    <Sparkles className="h-8 w-8 text-muted-foreground/30" />
                  </div>
                )}
                <Badge variant="secondary" className="absolute top-2 left-2 text-xs font-bold z-20">
                  {t("aiRecipe.dishNumber", "第 {{n}} 道", { n: index + 1 })}
                </Badge>
              </div>
            )}
            <CardHeader className="pb-2">
              <h4 className="font-semibold">{getLocalizedName(recipe)}</h4>
              {getLocalizedSubtitle(recipe) && (
                <p className="text-xs text-muted-foreground italic">{getLocalizedSubtitle(recipe)}</p>
              )}
            </CardHeader>
            <CardContent className="pt-0">
              <div className="flex flex-wrap gap-2">
                <Badge variant="outline" className="gap-1 text-xs">
                  <Clock className="h-3 w-3" />
                  {recipe.cook_time} {t("recipe.minutes", "分钟")}
                </Badge>
                <Badge variant="outline" className="gap-1 text-xs">
                  <ChefHat className="h-3 w-3" />
                  {difficultyLabel(recipe.difficulty)}
                </Badge>
                <Badge variant="outline" className="gap-1 text-xs">
                  <Users className="h-3 w-3" />
                  {recipe.servings} {t("recipe.servingsUnit", "人份")}
                </Badge>
              </div>

              {recipe.missing_ingredients && recipe.missing_ingredients.length > 0 && (
                <div className="mt-2 flex items-start gap-1.5 text-xs text-destructive">
                  <AlertTriangle className="h-3.5 w-3.5 mt-0.5 shrink-0" />
                  <span>
                    {t("aiRecipe.missingIngredients", "需要额外食材：")}
                    {(() => {
                      const lang = i18n.language;
                      const list =
                        lang === "en" ? (recipe as any).missing_ingredients_en || recipe.missing_ingredients :
                        lang === "pt" ? (recipe as any).missing_ingredients_pt || recipe.missing_ingredients :
                        recipe.missing_ingredients;
                      return list.join(lang === "zh" ? "、" : ", ");
                    })()}
                  </span>
                </div>
              )}
            </CardContent>
          </Card>
        ))}
      </div>

      {/* Editable notes */}
      <div className="space-y-2">
        {editingNotes ? (
          <div className="space-y-2">
            <label className="text-xs font-medium text-muted-foreground">
              {t("aiRecipe.editNotes", "编辑备注（修改后点击换一批即可重新生成）")}
            </label>
            <Textarea
              value={notesValue}
              onChange={(e) => setNotesValue(e.target.value)}
              placeholder={t("aiRecipe.notesPlaceholder", "例如：两菜一汤、清淡为主...")}
              className="min-h-[60px] text-sm"
            />
            <Button variant="ghost" size="sm" onClick={() => setEditingNotes(false)}>
              {t("common.done", "完成")}
            </Button>
          </div>
        ) : (
          <button
            onClick={() => setEditingNotes(true)}
            className="inline-flex items-center gap-1.5 text-xs text-muted-foreground hover:text-primary transition-colors"
          >
            <Pencil className="h-3 w-3" />
            {notesValue
              ? t("aiRecipe.currentNotes", "备注：{{notes}}", { notes: notesValue.length > 30 ? notesValue.slice(0, 30) + "…" : notesValue })
              : t("aiRecipe.addNotes", "添加备注（如：两菜一汤）")}
          </button>
        )}
      </div>

      {/* Action buttons */}
      <div className="flex gap-3 pt-2 sticky bottom-4">
        {onRegenerate && (
          <Button
            variant="outline"
            className="flex-1 gap-1.5"
            onClick={() => onRegenerate(notesValue || undefined)}
            disabled={isRegenerating}
          >
            <RefreshCw className={`h-4 w-4 ${isRegenerating ? "animate-spin" : ""}`} />
            {isRegenerating
              ? t("aiRecipe.regenerating", "重新生成中...")
              : t("aiRecipe.regenerateRecipes", "换一批")}
          </Button>
        )}
        <Button className="flex-1 gap-1.5" onClick={onConfirmMeal}>
          <Check className="h-4 w-4" />
          {t("aiRecipe.confirmMeal", "确认菜单")}
        </Button>
      </div>

      {/* History section */}
      {recipeHistory.length > 0 && onSelectHistoryMeal && (
        <div className="border rounded-xl overflow-hidden">
          <button
            className="w-full flex items-center justify-between px-4 py-3 text-sm font-medium hover:bg-muted/50 transition-colors"
            onClick={() => setShowHistory(h => !h)}
          >
            <span className="flex items-center gap-2 text-muted-foreground">
              <History className="h-4 w-4" />
              {t("aiRecipe.historyMeals", "历史菜单")}
              <Badge variant="secondary" className="text-xs">{recipeHistory.length}</Badge>
            </span>
            <ChevronDown className={`h-4 w-4 text-muted-foreground transition-transform ${showHistory ? "rotate-180" : ""}`} />
          </button>

          {showHistory && (
            <div className="divide-y">
              {recipeHistory.map((meal, hi) => (
                <div key={hi} className="px-4 py-3 space-y-1.5">
                  <div className="flex items-center justify-between">
                    <span className="text-xs text-muted-foreground">
                      {t("aiRecipe.historyEntry", "第 {{n}} 次生成", { n: recipeHistory.length - hi })}
                    </span>
                    <Button
                      size="sm"
                      variant="ghost"
                      className="h-7 text-xs gap-1 text-primary"
                      onClick={() => { onSelectHistoryMeal(meal); setShowHistory(false); }}
                    >
                      <Check className="h-3 w-3" />
                      {t("aiRecipe.useThisMeal", "选这桌")}
                    </Button>
                  </div>
                  <div className="flex flex-wrap gap-1.5">
                    {meal.map((dish, di) => (
                      <span key={di} className="text-xs bg-muted rounded-full px-2.5 py-0.5">
                        {i18n.language === "en" && dish.recipe_name_en ? dish.recipe_name_en
                          : i18n.language === "pt" && dish.recipe_name_pt ? dish.recipe_name_pt
                          : dish.recipe_name}
                      </span>
                    ))}
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default RecipeSelectStep;
