import { useState } from "react";
import { useTranslation } from "react-i18next";
import { Check, SkipForward } from "lucide-react";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { AIRecipeSuggestion } from "@/hooks/useAIRecipe";
import AIRecipeCookingView from "./AIRecipeCookingView";

interface MealCookingViewProps {
  recipes: AIRecipeSuggestion[];
  ingredientImageUrl: string;
  onComplete: (skippedIndices: number[]) => void;
  onBack: () => void;
}

const MealCookingView = ({ recipes, ingredientImageUrl, onComplete, onBack }: MealCookingViewProps) => {
  const { t, i18n } = useTranslation();
  const [currentDishIndex, setCurrentDishIndex] = useState(0);
  const [completedDishes, setCompletedDishes] = useState<Set<number>>(new Set());
  const [skippedDishes, setSkippedDishes] = useState<Set<number>>(new Set());

  const getLocalName = (recipe: AIRecipeSuggestion) => {
    if (i18n.language === "en" && recipe.recipe_name_en) return recipe.recipe_name_en;
    if (i18n.language === "pt" && recipe.recipe_name_pt) return recipe.recipe_name_pt;
    return recipe.recipe_name;
  };

  const isAllResolved = (completed: Set<number>, skipped: Set<number>) =>
    completed.size + skipped.size === recipes.length;

  const moveToNext = (completed: Set<number>, skipped: Set<number>, fromIndex: number) => {
    if (isAllResolved(completed, skipped)) {
      onComplete(Array.from(skipped));
      return;
    }
    // Find next unresolved dish
    for (let i = fromIndex + 1; i < recipes.length; i++) {
      if (!completed.has(i) && !skipped.has(i)) {
        setCurrentDishIndex(i);
        return;
      }
    }
    for (let i = 0; i < fromIndex; i++) {
      if (!completed.has(i) && !skipped.has(i)) {
        setCurrentDishIndex(i);
        return;
      }
    }
    onComplete(Array.from(skipped));
  };

  const handleDishComplete = () => {
    const newCompleted = new Set(completedDishes);
    newCompleted.add(currentDishIndex);
    setCompletedDishes(newCompleted);
    moveToNext(newCompleted, skippedDishes, currentDishIndex);
  };

  const handleSkipDish = () => {
    const newSkipped = new Set(skippedDishes);
    newSkipped.add(currentDishIndex);
    setSkippedDishes(newSkipped);
    moveToNext(completedDishes, newSkipped, currentDishIndex);
  };

  const doneDishes = completedDishes.size + skippedDishes.size;

  return (
    <div className="space-y-3">
      {/* Dish navigation tabs */}
      <div className="flex items-center gap-2 overflow-x-auto pb-1 scrollbar-hide">
        {recipes.map((recipe, index) => (
          <button
            key={index}
            onClick={() => setCurrentDishIndex(index)}
            className={`flex items-center gap-1.5 px-3 py-1.5 rounded-full text-xs font-medium whitespace-nowrap transition-all border shrink-0 ${
              currentDishIndex === index
                ? "bg-primary text-primary-foreground border-primary"
                : completedDishes.has(index)
                ? "bg-green-100 dark:bg-green-900/30 text-green-700 dark:text-green-400 border-green-300 dark:border-green-700"
                : skippedDishes.has(index)
                ? "bg-muted text-muted-foreground/50 border-border line-through"
                : "bg-muted text-muted-foreground border-border hover:border-primary/50"
            }`}
          >
            {completedDishes.has(index) && <Check className="h-3 w-3" />}
            {skippedDishes.has(index) && <SkipForward className="h-3 w-3" />}
            {getLocalName(recipe)}
          </button>
        ))}
      </div>

      {/* Progress */}
      <div className="text-center text-xs text-muted-foreground">
        {t("aiRecipe.cookingProgress", "已完成 {{done}}/{{total}} 道菜", {
          done: completedDishes.size,
          total: recipes.length,
        })}
        {skippedDishes.size > 0 && (
          <span className="ml-1 opacity-60">
            ({t("aiRecipe.skippedCount", "已跳过 {{n}} 道", { n: skippedDishes.size })})
          </span>
        )}
      </div>

      {/* Current dish cooking view - key resets state when switching dishes */}
      <AIRecipeCookingView
        key={currentDishIndex}
        recipe={recipes[currentDishIndex]}
        ingredientImageUrl={ingredientImageUrl}
        onComplete={handleDishComplete}
        onBack={onBack}
        onSkip={recipes.length > 1 && !skippedDishes.has(currentDishIndex) ? handleSkipDish : undefined}
      />
    </div>
  );
};

export default MealCookingView;
