import { useState, useEffect, useRef, useMemo } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate, useLocation } from "react-router-dom";
import { ArrowLeft, Loader2, Sparkles, Bookmark, X, Camera, SkipForward } from "lucide-react";
import { useRecipeVideo } from "@/hooks/useRecipeVideo";
import AnimatedLoadingText from "@/components/ai-recipe/AnimatedLoadingText";
import { Button } from "@/components/ui/button";
import { Progress } from "@/components/ui/progress";
import { useAIRecipe, IdentifiedIngredient, AIRecipeSuggestion } from "@/hooks/useAIRecipe";
import { useCreateRecipe } from "@/hooks/useCreateRecipe";
import { useSavedRecipes } from "@/hooks/useSavedRecipes";
import { useLoginRequired } from "@/hooks/useLoginRequired";
import { toast } from "sonner";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter } from "@/components/ui/dialog";
import IngredientPhotoStep from "@/components/ai-recipe/IngredientPhotoStep";
import IngredientConfirmStep from "@/components/ai-recipe/IngredientConfirmStep";
import PreferencesStep, { RecipePreferences } from "@/components/ai-recipe/PreferencesStep";
import RecipeSelectStep from "@/components/ai-recipe/RecipeSelectStep";
import MealDetailView from "@/components/ai-recipe/MealDetailView";
import MealCookingView from "@/components/ai-recipe/MealCookingView";
import ResultPhotoStep from "@/components/ai-recipe/ResultPhotoStep";
import AIRecipeSharePrompt from "@/components/ai-recipe/AIRecipeSharePrompt";
import PublishConfirmStep from "@/components/ai-recipe/PublishConfirmStep";

type Step = "photo" | "confirm" | "preferences" | "generating" | "select" | "detail" | "cooking" | "share-prompt" | "result" | "publish";

const STEP_ORDER: Step[] = ["photo", "confirm", "preferences", "generating", "select", "detail", "cooking", "share-prompt", "result", "publish"];

const SESSION_KEY = "ai-recipe-session";

interface SessionState {
  step: Step;
  ingredientImageUrls: string[];
  ingredients: IdentifiedIngredient[];
  preferences: RecipePreferences;
  recipes: AIRecipeSuggestion[];
  publishingDishIndex: number;
  publishQueue: number[];
  resultImageUrl: string;
  photoVerified: boolean;
}

const loadSession = (): Partial<SessionState> => {
  try {
    const raw = sessionStorage.getItem(SESSION_KEY);
    if (raw) return JSON.parse(raw);
  } catch {}
  return {};
};

const AIRecipe = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const location = useLocation();
  const backTo = (location.state as any)?.from || "/ai-recipe";
  const { requireLogin } = useLoginRequired();
  const { identifyIngredients, generateRecipes, generateRecipeImages, verifyResultPhoto, uploadImage, isIdentifying, isGenerating, isVerifying, isGeneratingImages } = useAIRecipe();
  const [showCloseConfirm, setShowCloseConfirm] = useState(false);
  const createRecipe = useCreateRecipe();
  const { saveRecipe, deleteRecipe, isSaving } = useSavedRecipes();
  const { videoTasks, videoStates, generateVideo, selectVideo, clearTask } = useRecipeVideo();

  const [saved] = useState(() => loadSession());
  const hasExistingSession = !!(saved.step && saved.step !== "photo" && saved.step !== "generating" && (saved.ingredients?.length || saved.recipes?.length));

  const [showResumeDialog, setShowResumeDialog] = useState(hasExistingSession);
  const [sessionActive, setSessionActive] = useState(!hasExistingSession);

  const [step, setStep] = useState<Step>("photo");
  const [ingredientImageUrls, setIngredientImageUrls] = useState<string[]>([]);
  const [ingredients, setIngredients] = useState<IdentifiedIngredient[]>([]);
  const [preferences, setPreferences] = useState<RecipePreferences>({});
  const [recipes, setRecipes] = useState<AIRecipeSuggestion[]>([]);
  const recipesRef = useRef<AIRecipeSuggestion[]>([]);
  const [recipeHistory, setRecipeHistory] = useState<AIRecipeSuggestion[][]>([]);
  const [publishingDishIndex, setPublishingDishIndex] = useState<number>(0);
  const [publishQueue, setPublishQueue] = useState<number[]>([]);
  const [resultImageUrl, setResultImageUrl] = useState<string>("");
  const [photoVerified, setPhotoVerified] = useState(false);
  const [resultNote, setResultNote] = useState<string>("");
  const [skippedCookingIndices, setSkippedCookingIndices] = useState<number[]>([]);

  const handleResumeSession = () => {
    setStep(saved.step && saved.step !== "generating" ? saved.step : "photo");
    setIngredientImageUrls(saved.ingredientImageUrls || []);
    setIngredients(saved.ingredients || []);
    setPreferences(saved.preferences || {});
    const savedRecipes = saved.recipes || [];
    recipesRef.current = savedRecipes;
    setRecipes(savedRecipes);
    setPublishingDishIndex(saved.publishingDishIndex || 0);
    setPublishQueue(saved.publishQueue || []);
    setResultImageUrl(saved.resultImageUrl || "");
    setPhotoVerified(saved.photoVerified || false);
    setSessionActive(true);
    setShowResumeDialog(false);
  };

  // Keep ref always in sync with recipes state — most reliable way to avoid stale closures
  useEffect(() => {
    recipesRef.current = recipes;
  }, [recipes]);

  const handleNewSession = () => {
    sessionStorage.removeItem(SESSION_KEY);
    // Clear all video tasks from previous session
    Object.keys(videoTasks).forEach(key => clearTask(key));
    setSessionActive(true);
    setShowResumeDialog(false);
  };

  const stepIndex = STEP_ORDER.indexOf(step);
  const progress = ((stepIndex + 1) / STEP_ORDER.length) * 100;

  // Persist state to sessionStorage (only when session is active to avoid overwriting with defaults)
  useEffect(() => {
    if (!sessionActive) return;
    try {
      const lightRecipes = recipes.map(r => ({
        ...r,
        image_url: r.image_url && r.image_url.startsWith("data:") ? undefined : r.image_url,
      }));
      const state: SessionState = { step, ingredientImageUrls, ingredients, preferences, recipes: lightRecipes as any, publishingDishIndex, publishQueue, resultImageUrl, photoVerified };
      sessionStorage.setItem(SESSION_KEY, JSON.stringify(state));
    } catch (e) {
      console.warn("Session save failed (quota):", e);
    }
  }, [sessionActive, step, ingredientImageUrls, ingredients, preferences, recipes, publishingDishIndex, publishQueue, resultImageUrl, photoVerified]);

  // Scroll to top when step changes
  useEffect(() => {
    window.scrollTo({ top: 0, behavior: "instant" });
  }, [step]);

  const currentPublishRecipe = publishQueue.length > 0 ? recipes[publishQueue[0]] : null;
  const currentQueuePosition = publishQueue.length > 0 ? 1 : 0;
  // Step 1a: Photos uploaded → identify
  const handlePhotosUploaded = async (urls: string[]) => {
    if (!requireLogin()) return;
    setIngredientImageUrls(urls);
    const allIngredients: IdentifiedIngredient[] = [];
    const seen = new Set<string>();
    for (const url of urls) {
      const result = await identifyIngredients(url);
      if (result) {
        for (const ing of result) {
          if (!seen.has(ing.name)) {
            seen.add(ing.name);
            allIngredients.push(ing);
          }
        }
      }
    }
    if (allIngredients.length > 0) {
      setIngredients(allIngredients);
      setStep("confirm");
    }
  };

  const handleSkipToManual = () => {
    if (!requireLogin()) return;
    setIngredients([]);
    setStep("confirm");
  };

  const handleConfirmIngredients = () => {
    if (ingredients.length === 0) {
      toast.error(t("aiRecipe.noIngredients", "请至少保留一种食材"));
      return;
    }
    setStep("preferences");
  };

  const handlePreferencesConfirm = async (prefs: RecipePreferences) => {
    setPreferences(prefs);
    await doGenerate(prefs);
  };

  const handlePreferencesSkip = async () => {
    setPreferences({});
    await doGenerate({});
  };

  const doGenerate = async (prefs: RecipePreferences, excludeDishes?: string[]) => {
    setStep("generating");
    // Snapshot current recipes from ref before async call (ref is always latest via useEffect)
    const currentBeforeGenerate = recipesRef.current.slice();
    const result = await generateRecipes(ingredients, prefs.difficulty, prefs.servings, prefs.dishCount, prefs.notes, excludeDishes);
    if (result && result.length > 0) {
      // Push snapshot into history only if non-empty and not a dup of history top
      if (currentBeforeGenerate.length > 0) {
        setRecipeHistory(prev => {
          const isDup =
            prev.length > 0 &&
            prev[0].length === currentBeforeGenerate.length &&
            prev[0][0]?.recipe_name === currentBeforeGenerate[0]?.recipe_name;
          if (isDup) return prev;
          return [currentBeforeGenerate, ...prev].slice(0, 5);
        });
      }
      setRecipes(result);
      setStep("select");

      // Asynchronously generate images — update recipes as each image arrives
      generateRecipeImages(
        result.map(r => ({ recipe_name: r.recipe_name, recipe_name_en: r.recipe_name_en })),
        (recipeName, imageUrl, imageSource) => {
          setRecipes(prev =>
            prev.map(r =>
              r.recipe_name === recipeName
                ? { ...r, image_url: imageUrl, image_source: imageSource as "ai" | "web" }
                : r
            )
          );
        }
      );
    } else {
      setStep("preferences");
    }
  };

  // Confirm entire meal
  const handleConfirmMeal = () => {
    setStep("detail");
  };

  // Save all recipes
  const handleSaveAllRecipes = async () => {
    for (const recipe of recipes) {
      await saveRecipe(recipe);
    }
  };

  const handleStartCooking = () => {
    // Clear any existing video tasks when starting/restarting cooking
    recipes.forEach(r => clearTask(r.recipe_name));
    setStep("cooking");
  };

  const handleCookingComplete = (skippedIndices: number[]) => {
    setSkippedCookingIndices(skippedIndices);
    setStep("share-prompt");
  };

  const [showSaveBeforeAction, setShowSaveBeforeAction] = useState<"share" | "skip" | null>(null);

  const handleWantToShare = (selectedIndices: number[]) => {
    setPublishQueue(selectedIndices);
    setShowSaveBeforeAction("share");
  };

  const handleSkipShare = () => {
    sessionStorage.removeItem(SESSION_KEY);
    toast.success(t("aiRecipe.cookingComplete", "太棒了！烹饪完成！"));
    navigate("/community");
  };

  const handleSaveAndContinue = async (action: "share" | "skip") => {
    for (const recipe of recipes) {
      await saveRecipe(recipe);
    }
    setShowSaveBeforeAction(null);
    if (action === "share") {
      setPublishingDishIndex(publishQueue[0] || 0);
      setStep("result");
    } else {
      handleSkipShare();
    }
  };

  const handleSkipSaveAndContinue = (action: "share" | "skip") => {
    setShowSaveBeforeAction(null);
    if (action === "share") {
      setPublishingDishIndex(publishQueue[0] || 0);
      setStep("result");
    } else {
      handleSkipShare();
    }
  };

  // Result photo verified
  const handlePhotoVerified = (url: string, verified: boolean, note?: string) => {
    setResultImageUrl(url);
    setPhotoVerified(verified);
    if (note) setResultNote(note);
    if (verified) {
      setStep("publish");
    }
  };

  // Publish current dish
  const handlePublish = async (notes?: string) => {
    if (!currentPublishRecipe || !resultImageUrl) return;

    try {
      await createRecipe.mutateAsync({
        recipe_name: currentPublishRecipe.recipe_name,
        recipe_name_en: currentPublishRecipe.recipe_name_en,
        recipe_name_pt: currentPublishRecipe.recipe_name_pt,
        subtitle: currentPublishRecipe.subtitle,
        subtitle_en: currentPublishRecipe.subtitle_en,
        subtitle_pt: currentPublishRecipe.subtitle_pt,
        cook_time: currentPublishRecipe.cook_time,
        difficulty: currentPublishRecipe.difficulty,
        servings: currentPublishRecipe.servings,
        ingredients: currentPublishRecipe.ingredients,
        steps: currentPublishRecipe.steps.map((s, i) => ({ ...s, order: i + 1 })),
        cover_image: resultImageUrl,
        introduction: currentPublishRecipe.introduction || "",
        introduction_en: currentPublishRecipe.introduction_en,
        introduction_pt: currentPublishRecipe.introduction_pt,
        cooking_note: notes?.trim() || resultNote || undefined,
        ai_recipe_source: {
          source: "ai_recipe",
          ingredient_images: ingredientImageUrls,
          identified_ingredients: ingredients,
          selected_recipe_index: publishQueue[0],
          all_recipes: recipes.map(r => r.recipe_name),
          result_image: resultImageUrl,
          photo_verified: photoVerified,
        },
      });

      // Remove current from queue and move to next
      const remaining = publishQueue.slice(1);
      setPublishQueue(remaining);

      if (remaining.length > 0) {
        toast.success(t("aiRecipe.dishPublished", "第 {{n}} 道菜已发布！继续下一道", { n: currentQueuePosition }));
        setPublishingDishIndex(remaining[0]);
        setResultImageUrl("");
        setPhotoVerified(false);
        setResultNote("");
        setStep("result");
      } else {
        sessionStorage.removeItem(SESSION_KEY);
        toast.success(t("aiRecipe.allPublished", "所有菜谱已发布到社区！"));
        navigate("/community");
      }
    } catch (err) {
      // Error handled in hook
    }
  };

  // Skip current dish in publish queue
  const handleSkipCurrentDish = () => {
    const remaining = publishQueue.slice(1);
    setPublishQueue(remaining);

    if (remaining.length > 0) {
      setPublishingDishIndex(remaining[0]);
      setResultImageUrl("");
      setPhotoVerified(false);
      setResultNote("");
      setStep("result");
    } else {
      sessionStorage.removeItem(SESSION_KEY);
      toast.success(t("aiRecipe.publishDone", "发布完成！"));
      navigate("/community");
    }
  };

  const handleBack = () => {
    switch (step) {
      case "confirm": setStep("photo"); break;
      case "preferences": setStep("confirm"); break;
      case "select": setStep("preferences"); break;
      case "detail": setStep("select"); break;
      case "cooking": setStep("detail"); break;
      case "share-prompt": setStep("cooking"); break;
      case "result": setStep("share-prompt"); break;
      case "publish": setStep("result"); break;
      default:
        navigate("/ai-recipe", { replace: true, state: { from: backTo } });
        break;
    }
  };

  const getStepTitle = () => {
    switch (step) {
      case "photo": return t("aiRecipe.stepPhoto", "拍摄食材");
      case "confirm": return t("aiRecipe.stepConfirm", "确认食材");
      case "preferences": return t("aiRecipe.stepPreferences", "烹饪偏好");
      case "generating": return t("aiRecipe.stepGenerating", "生成菜谱");
      case "select": return t("aiRecipe.stepMealPlan", "完整菜单");
      case "detail": return t("aiRecipe.stepDetail", "菜谱详情");
      case "cooking": return t("aiRecipe.stepCooking", "烹饪中");
      case "share-prompt": return t("aiRecipe.stepShare", "分享");
      case "result": return currentPublishRecipe
        ? t("aiRecipe.stepResultDish", "拍摄成品 ({{n}}/{{total}})", { n: currentQueuePosition, total: publishQueue.length + currentQueuePosition - 1 })
        : t("aiRecipe.stepResult", "拍摄成品");
      case "publish": return t("aiRecipe.stepPublish", "发布");
    }
  };

  const getResumeStepLabel = () => {
    const s = saved.step;
    if (s === "select" || s === "detail") return t("aiRecipe.resumeAtSelect", "已生成菜单");
    if (s === "cooking") return t("aiRecipe.resumeAtCooking", "烹饪中");
    if (s === "confirm") return t("aiRecipe.resumeAtConfirm", "食材确认");
    if (s === "preferences") return t("aiRecipe.resumeAtPrefs", "烹饪偏好");
    if (s === "share-prompt" || s === "result" || s === "publish") return t("aiRecipe.resumeAtShare", "分享/发布");
    return "";
  };

  return (
    <div className="min-h-screen flex flex-col bg-background">
      {/* Resume session dialog - blocks main content until user decides */}
      {showResumeDialog ? (
        <Dialog open={showResumeDialog} onOpenChange={(open) => { if (!open) { setShowResumeDialog(false); navigate('/community', { state: { openCreatePost: true } }); } }}>
          <DialogContent>
            <DialogHeader>
              <DialogTitle className="flex items-center gap-2">
                <Sparkles className="h-5 w-5 text-primary" />
                {t("aiRecipe.resumeTitle", "检测到未完成的食谱")}
              </DialogTitle>
              <DialogDescription className="space-y-2">
                <span>{t("aiRecipe.resumeDesc", "您有一个未完成的 AI 食谱流程，是否要继续？")}</span>
                {saved.recipes && saved.recipes.length > 0 && (
                  <span className="block text-xs mt-2 bg-muted rounded-lg px-3 py-2">
                    {saved.recipes.map(r => r.recipe_name).join("、")}
                  </span>
                )}
                <span className="block text-xs text-muted-foreground">
                  {t("aiRecipe.resumeProgress", "进度：{{step}}", { step: getResumeStepLabel() })}
                </span>
              </DialogDescription>
            </DialogHeader>
            <DialogFooter className="flex-col sm:flex-row gap-2">
              <Button variant="outline" onClick={handleNewSession}>
                {t("aiRecipe.startNew", "重新开始")}
              </Button>
              <Button onClick={handleResumeSession}>
                {t("aiRecipe.resumeContinue", "继续上次")}
              </Button>
            </DialogFooter>
          </DialogContent>
        </Dialog>
      ) : null}
      {!showResumeDialog && <>
      {/* Header */}
      <div className="px-4 py-3 border-b bg-background shrink-0 sticky top-14 z-10">
        <div className="flex items-center justify-between mb-2">
          <button
            onClick={step === "publish" || step === "share-prompt" ? () => setShowCloseConfirm(true) : handleBack}
            className="p-1 -ml-1 hover:bg-muted rounded-md"
          >
            {step === "publish" || step === "share-prompt" ? <X className="h-5 w-5" /> : <ArrowLeft className="h-5 w-5" />}
          </button>
          <div className="flex items-center gap-2">
            <Sparkles className="h-4 w-4 text-primary" />
            <h2 className="font-medium">{getStepTitle()}</h2>
          </div>
          <div className="w-6" />
        </div>
        <Progress value={progress} className="h-1" />
      </div>

      {/* Content */}
      <div className="flex-1 overflow-y-auto p-4 max-w-lg mx-auto w-full">
        {step === "photo" && (
          <IngredientPhotoStep
            onPhotosUploaded={handlePhotosUploaded}
            onSkipToManual={handleSkipToManual}
            uploadImage={uploadImage}
            isUploading={isIdentifying}
            initialImageUrls={ingredientImageUrls}
            onRequireLogin={requireLogin}
          />
        )}

        {isIdentifying && (
          <div className="flex flex-col items-center gap-3 py-12">
            <Loader2 className="h-8 w-8 animate-spin text-primary" />
            <AnimatedLoadingText
              messages={[
                t("aiRecipe.identifying.1", "AI 正在识别食材..."),
                t("aiRecipe.identifying.2", "分析食材种类与数量..."),
                t("aiRecipe.identifying.3", "即将完成识别..."),
              ]}
            />
          </div>
        )}

        {step === "confirm" && !isIdentifying && (
          <div className="space-y-4">
            <IngredientConfirmStep ingredients={ingredients} onChange={setIngredients} />
            <Button className="w-full" onClick={handleConfirmIngredients}>
              {t("aiRecipe.nextStep", "下一步")}
            </Button>
          </div>
        )}

        {step === "preferences" && (
          <PreferencesStep
            onConfirm={handlePreferencesConfirm}
            onSkip={handlePreferencesSkip}
            initialValues={preferences}
          />
        )}

        {step === "generating" && (
          <div className="flex flex-col items-center gap-3 py-12">
            <Loader2 className="h-8 w-8 animate-spin text-primary" />
            <AnimatedLoadingText
              messages={[
                t("aiRecipe.generating.1", "AI 正在为您设计一桌菜..."),
                t("aiRecipe.generating.2", "搭配营养均衡的食谱..."),
                t("aiRecipe.generating.3", "计算最佳烹饪顺序..."),
                t("aiRecipe.generating.4", "生成详细烹饪步骤..."),
                t("aiRecipe.generating.5", "即将完成，请稍候..."),
                t("aiRecipe.generating.6", "挑选最佳调味搭配..."),
                t("aiRecipe.generating.7", "优化烹饪火候与温度..."),
                t("aiRecipe.generating.8", "加入大厨私藏小窍门..."),
                t("aiRecipe.generating.9", "精调每道菜的用量..."),
                t("aiRecipe.generating.10", "打磨最终菜谱细节..."),
              ]}
            />
          </div>
        )}

        {step === "select" && (
          <RecipeSelectStep
            recipes={recipes}
            recipeHistory={recipeHistory}
            onSelectHistoryMeal={(meal) => {
              // Snapshot current via ref (always up-to-date due to useEffect)
              const current = recipesRef.current.slice();
              setRecipeHistory(prev => {
                // Remove selected meal from history
                const filtered = prev.filter(h => h !== meal);
                // Push current into history (deduplicated)
                if (current.length === 0) return filtered;
                const isDup = filtered.length > 0 && filtered[0][0]?.recipe_name === current[0]?.recipe_name && filtered[0].length === current.length;
                return isDup ? filtered : [current, ...filtered].slice(0, 5);
              });
              setRecipes(meal); // useEffect will sync recipesRef.current
            }}
            onConfirmMeal={handleConfirmMeal}
            onRegenerate={(updatedNotes?: string) => {
              const excludeNames = recipes.map(r => r.recipe_name);
              const updatedPrefs = { ...preferences, notes: updatedNotes !== undefined ? updatedNotes : preferences.notes };
              setPreferences(updatedPrefs);
              doGenerate(updatedPrefs, excludeNames);
            }}
            isRegenerating={isGenerating}
            preferences={preferences}
            isLoadingImages={isGeneratingImages}
          />
        )}

        {step === "detail" && recipes.length > 0 && (
          <div className="space-y-4">
            <MealDetailView recipes={recipes} />
            <div className="flex gap-3">
              <Button variant="outline" className="flex-1" onClick={() => setStep("select")}>
                {t("aiRecipe.changeMeal", "换一桌")}
              </Button>
              <Button
                variant="outline"
                className="gap-1"
                onClick={handleSaveAllRecipes}
                disabled={isSaving}
              >
                <Bookmark className="h-4 w-4" />
                {t("aiRecipe.saveAll", "全部保存")}
              </Button>
              <Button className="flex-1" onClick={handleStartCooking}>
                {t("aiRecipe.startCooking", "开始烹饪")}
              </Button>
            </div>
          </div>
        )}

        {step === "cooking" && recipes.length > 0 && (
          <MealCookingView
            recipes={recipes}
            ingredientImageUrl={ingredientImageUrls[0] || ""}
            onComplete={handleCookingComplete}
            onBack={() => setStep("detail")}
          />
        )}

        {step === "share-prompt" && recipes.length > 0 && (
          <div className="space-y-4">
            <AIRecipeSharePrompt
              recipes={recipes}
              skippedIndices={skippedCookingIndices}
              onShare={handleWantToShare}
              onSkip={() => setShowSaveBeforeAction("skip")}
              onSaveRecipe={saveRecipe}
              onUnsaveRecipe={deleteRecipe}
              isSaving={isSaving}
            />
          </div>
        )}

        {/* Save before action dialog */}
        <AlertDialog open={!!showSaveBeforeAction} onOpenChange={(open) => !open && setShowSaveBeforeAction(null)}>
          <AlertDialogContent>
            <AlertDialogHeader>
              <AlertDialogTitle>{t("aiRecipe.saveBeforeTitle", "保存食谱？")}</AlertDialogTitle>
              <AlertDialogDescription>
                {t("aiRecipe.saveBeforeDescMeal", "是否将这桌菜的所有食谱保存到您的收藏中？")}
              </AlertDialogDescription>
            </AlertDialogHeader>
            <AlertDialogFooter className="flex-col sm:flex-row gap-2">
              <AlertDialogCancel onClick={() => showSaveBeforeAction && handleSkipSaveAndContinue(showSaveBeforeAction)}>
                {t("aiRecipe.noSave", "不保存")}
              </AlertDialogCancel>
              <AlertDialogAction
                onClick={() => showSaveBeforeAction && handleSaveAndContinue(showSaveBeforeAction)}
                disabled={isSaving}
              >
                <Bookmark className="h-4 w-4 mr-1" />
                {t("aiRecipe.saveAllRecipes", "保存全部食谱")}
              </AlertDialogAction>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialog>

        {step === "result" && currentPublishRecipe && (
          <div className="space-y-4">
            {/* Current dish indicator */}
            {publishQueue.length > 0 && (
              <div className="flex items-center gap-3 p-3 rounded-xl border bg-muted/30">
                <div className="w-12 h-12 rounded-lg overflow-hidden bg-muted shrink-0">
                  {currentPublishRecipe.image_url ? (
                    <img src={currentPublishRecipe.image_url} alt="" className="w-full h-full object-cover" />
                  ) : (
                    <div className="w-full h-full flex items-center justify-center text-lg">🍽️</div>
                  )}
                </div>
                <div className="flex-1 min-w-0">
                  <p className="font-medium text-sm truncate">{currentPublishRecipe.recipe_name}</p>
                  {currentPublishRecipe.subtitle && (
                    <p className="text-xs text-muted-foreground italic truncate">{currentPublishRecipe.subtitle}</p>
                  )}
                </div>
                {publishQueue.length > 1 && (
                  <Button variant="ghost" size="sm" className="text-xs shrink-0" onClick={handleSkipCurrentDish}>
                    <SkipForward className="h-3.5 w-3.5 mr-1" />
                    {t("aiRecipe.skipThisDish", "跳过")}
                  </Button>
                )}
              </div>
            )}
            <ResultPhotoStep
              onPhotoVerified={handlePhotoVerified}
              uploadImage={uploadImage}
              verifyPhoto={verifyResultPhoto}
              recipeName={currentPublishRecipe.recipe_name}
              isVerifying={isVerifying}
            />
          </div>
        )}

        {step === "publish" && currentPublishRecipe && (
          <PublishConfirmStep
            resultImageUrl={resultImageUrl}
            recipeName={currentPublishRecipe.recipe_name}
            initialNote={resultNote}
            onPublish={handlePublish}
            onSkip={handleSkipCurrentDish}
            isPublishing={createRecipe.isPending}
            recipeSteps={currentPublishRecipe.steps}
            videoTask={videoTasks[currentPublishRecipe.recipe_name]}
            videoState={videoStates[currentPublishRecipe.recipe_name]}
            onGenerateVideo={(prompt) => generateVideo(currentPublishRecipe.recipe_name, resultImageUrl, prompt)}
            onSelectVideo={(index) => selectVideo(currentPublishRecipe.recipe_name, index)}
          />
        )}

        {/* Close confirmation dialog */}
        <AlertDialog open={showCloseConfirm} onOpenChange={setShowCloseConfirm}>
          <AlertDialogContent>
            <AlertDialogHeader>
              <AlertDialogTitle>
                {step === "share-prompt"
                  ? t("aiRecipe.exitShareTitle", "确认退出？")
                  : t("aiRecipe.closeConfirmTitle", "确认离开？")}
              </AlertDialogTitle>
              <AlertDialogDescription>
                {step === "share-prompt"
                  ? t("aiRecipe.exitShareDesc", "您还没有保存食谱，退出后食谱将丢失。是否先保存？")
                  : t("aiRecipe.closeConfirmDesc", "离开后将放弃发布，您的成品照片和食谱将不会保存到社区。")}
              </AlertDialogDescription>
            </AlertDialogHeader>
            <AlertDialogFooter className="flex-col sm:flex-row gap-2">
              <AlertDialogCancel>{t("common.cancel", "取消")}</AlertDialogCancel>
              {step === "share-prompt" ? (
                <>
                  <AlertDialogAction
                    onClick={async () => {
                      for (const recipe of recipes) {
                        await saveRecipe(recipe);
                      }
                      setShowCloseConfirm(false);
                      handleSkipShare();
                    }}
                    disabled={isSaving}
                  >
                    <Bookmark className="h-4 w-4 mr-1" />
                    {t("aiRecipe.saveAndExit", "保存并退出")}
                  </AlertDialogAction>
                  <AlertDialogAction
                    className="bg-destructive text-destructive-foreground hover:bg-destructive/90"
                    onClick={() => {
                      setShowCloseConfirm(false);
                      handleSkipShare();
                    }}
                  >
                    {t("aiRecipe.exitWithoutSave", "不保存，直接退出")}
                  </AlertDialogAction>
                </>
              ) : (
                <AlertDialogAction onClick={handleSkipShare}>
                  {t("aiRecipe.confirmLeave", "确认离开")}
                </AlertDialogAction>
              )}
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialog>
      </div>
      </>}
    </div>
  );
};

export default AIRecipe;
